.contain {
    display: flex;
    justify-content: space-between;
    height: 101px;
    background: #FFFFFF;
    box-shadow: 0 10px 4px rgba(0, 0, 0, 0.25);
}

.image {
    width: 261px;
    height: 101px;
}

.pages {
    height: 39px;
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 39px;
    display: flex;
    align-items: flex-end;
    text-align: center;
    color: rgba(0, 0, 0, 0.68);
    padding: 0 30px;
    border-radius: 15px;
    text-decoration: none;
    margin-top: 30px;
}

.pages:hover {
    /*text-decoration: underline #42D8E4;*/
    border-bottom: 5px solid #42D8E4;
}

.pagesActive {
    height: 39px;
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 39px;
    display: flex;
    align-items: flex-end;
    text-align: center;
    color: rgba(0, 0, 0, 0.68);
    padding: 0 30px;
    border-radius: 15px;
    text-decoration: none;
    margin-top: 30px;
    border-bottom: 5px solid #42D8E4;
}

.auth {
    cursor: pointer;
    text-decoration: none;
    height: 39px;
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 39px;
    display: flex;
    align-items: flex-end;
    text-align: center;
    color: rgba(0, 0, 0, 0.68);
    padding: 30px 20px 0 0;
}

.dropDownContain {
    width: 330px;
    text-align: center;
    border: 2px solid #42D8E4;
    border-radius: 10px;
    padding: 5px 65px 5px 65px;
      font-size: large;
    color: #000000;
}

.dropDownText {
    font-size: large;
    color: #000000;
    margin-right: 5px;
}

.dropDownIcon {
    color: #000000;

}