.mainContain {
    width: 1600px;
    height: auto;
    min-height: 850px;
    border: 5px solid #42D8E4;
    filter: drop-shadow(5px 5px 4px rgba(0, 0, 0, 0.25));
    border-radius: 15px;
    padding: 15px;
    margin: 125px auto;
}
.mainContainToOrg{
     width: 1600px;
    height: auto;
    min-height: 650px;
    border: 5px solid #42D8E4;
    filter: drop-shadow(5px 5px 4px rgba(0, 0, 0, 0.25));
    border-radius: 15px;
    padding: 15px;
    margin: 125px auto;
}
.mainContainToRef{
     width: 1600px;
    height: auto;
    min-height: 400px;
    border: 5px solid #42D8E4;
    filter: drop-shadow(5px 5px 4px rgba(0, 0, 0, 0.25));
    border-radius: 15px;
    padding: 15px;
    margin: 150px auto;
}
.contain {
    height: auto;
    display: flex;
    margin-bottom: 15px;
    margin-left: 100px;
    padding: 5px;
}

.mainText {
    width: 733px;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 800;
    font-size: 38px;
    line-height: 23px;
    align-items: center;
    text-align: left;
    color: #000000;
    margin-right: 300px;
    margin-left: 5px;
}

.myButt {
    width: auto;
    height: 57px;
    min-width: 165px;
    padding: 0 15px;
    gap: 10px;
    font-size: large;
    font-weight: bold;
    background: #42D8E4;
    box-shadow: 0 3px 0 rgba(0, 0, 0, 0.24);
    border-radius: 7px;
    color: #FFFFFF;
    margin: 34px 0 0 30px;
}

.topContain {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
    margin-left: 90px;
    align-content: end;
}

.topButt {
    width: auto;
    height: 57px;
    padding: 0 15px;
    gap: 10px;
    font-size: large;
    font-weight: bold;
    background: #42D8E4;
    box-shadow: 0 3px 0 rgba(0, 0, 0, 0.24);
    border-radius: 7px;
    color: #FFFFFF;
    margin: 34px 100px 15px 0;
}
.topButt:hover{
     background: #FFFFFF !important;
    color: #42D8E4 !important;
    border: 2px solid #42D8E4 !important;
}
.myButt:hover {
    background: #FFFFFF !important;
    color: #42D8E4 !important;
    border: 2px solid #42D8E4 !important;
}

.text {
    max-width: 193px;
    width: auto;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 22px;
    align-items: center;
    text-align: right;
    color: #000000;
    margin: 7px 15px 0 10px;
}

.inp {
    max-width: 500px;
    border: 2px solid #42D8E4;
    height: 40px;
    font-size: large;
}

.shortInp {
    max-width: 150px;
    border: 2px solid #42D8E4;
    height: 40px;
    font-size: large;
}

.datePick {
    border: 2px solid #42D8E4;
    font-size: large;
}

.textArea {
    max-width: 200px;
    border-radius: 15px;
    border: 2px solid #42D8E4;
    margin: 0 60px 0 0;
    font-size: large;
}

.select {
    border: 2px solid #42D8E4;
    border-radius: 11px;
    font-size: large;
    min-width: 200px;
}

.myTable {
    min-width: 300px;
    max-width: 2200px;
    margin: 0 auto 100px auto;
    font-size: x-large;
    border: #42D8E4 3px solid;
}

.tableText {
    font-size: large;
    color: #42D8E4;
}

.myColumn {
    background-color: #C5F4F8;
    border: #42D8E4 1px solid;
    border-top: none;
    font-size: large;
}

.tablePart {
    min-width: 300px;
    max-width: 2200px;
    font-size: x-large;
}

.tablePartSelect {
    width: 100%;
    background-color: #C5F4F8;
    color: #C5F4F8;

}

.dropDownTablePart {
    background-color: #C5F4F8;
}

.tablePartRow {
    padding: 0;
}
.expelled {
    text-decoration: line-through;

}