.containMain {
    width: 1600px;
    height: 854px;
    margin: 125px auto;
    display: flex;
    flex-direction: column;
    border: 5px solid #42D8E4;
    filter: drop-shadow(5px 5px 4px rgba(0, 0, 0, 0.25));
    border-radius: 15px;
    padding: 15px
}

.contain {
    display: flex;
    margin-left: 50px;
    margin-bottom: 25px !important;
}
.contain label {
    font-size: 26px !important;
    font-family: 'Roboto', serif;
    font-style: normal;
}

.mainText {
    width: 733px;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 800;
    font-size: 38px;
    line-height: 23px;
    align-items: center;
    text-align: center;
    color: #000000;
    margin-right: 300px;
}

.myButt {
    width: auto;
    min-width: 176px;
    height: 57px;
    padding: 0 15px;
    gap: 10px;
    font-size: large;
    font-weight: bold;
    background: #42D8E4;
    box-shadow: 0 3px 0 rgba(0, 0, 0, 0.24);
    border-radius: 7px;
    color: #FFFFFF;
    margin: 34px 0 0 40px;
}

.myButt:hover {
    background: #FFFFFF !important;
    color: #42D8E4 !important;
    border: 2px solid #42D8E4 !important;
}

.text {
    width: 193px;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 22px;
    align-items: center;
    text-align: right;
    color: #000000;
    margin: 7px 15px 0 100px;
}

.midText {
    width: 250px;
    font-family: 'Roboto', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 12px;
    align-items: center;
    text-align: center;
    color: #000000;
    margin: 15px 15px 15px 100px;
}

.select {
    border: 2px solid #42D8E4;
    border-radius: 11px;
    font-size: large;
    min-width: 200px;
}

.inp {
    max-width: 500px;
    border: 2px solid #42D8E4;
    height: 40px;
    font-size: large;
}

.textArea {
    max-width: 500px;
    margin-left: 160px;
    height: 150px;
    border: 2px solid #42D8E4;
    font-size: 20px;
    border-radius: 10px;
}