.contain {
    width: 447px;
    height: 137px;
    background: #FFFFFF;
    border: 5px solid #42D8E4;
    border-radius: 20px;
    margin-bottom: 70px;
    cursor: pointer;
}

.mainContain {
    margin: 80px auto;
    width: 1800px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    filter: drop-shadow(5px 5px 4px rgba(0, 0, 0, 0.25));

}

.refText {
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 39px;
    margin: 45px auto auto 10px;
    color: #42D8E4;


}