.contain {
    width: 579px;
    height: 596px;
    margin: 100px auto 0;
    background: #FFFFFF;
    border: 5px dashed rgba(66, 216, 228, 0.6);
    border-radius: 20px;

}

.mainText {

    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 39px;
    align-items: center;
    text-align: center;
    color: rgba(0, 0, 0, 0.68);
    margin: 100px auto 120px;
    padding-top: 15px;
}

.text {
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    align-items: center;
    text-align: center;
    color: rgba(0, 0, 0, 0.68);
    margin: 40px auto;
    padding: 0;
}

.inp {
    width: 216px;
    height: 40px;
    border: 2px solid #42D8E4;
    margin-left: 15px;
}

.inpPass {
    width: 216px;
    height: 40px;
    border: 2px solid #42D8E4;
    margin-left: 8px;
}

.myButt {
    width: 114px;
    height: 40px;
    padding: 0 15px;
    gap: 10px;
    font-size: large;
    font-weight: bold;
    background: #42D8E4;
    box-shadow: 0 3px 0 rgba(0, 0, 0, 0.24);
    border-radius: 7px;
    color: #FFFFFF;
}

.myButt:hover {
    background: #FFFFFF !important;
    color: #42D8E4 !important;
    border: 2px solid #42D8E4 !important;
}
.error {
    color: red;
    margin: 10px auto 0 auto;
    padding-left: 40px;
    width: 200px;
}